import React, { useState } from "react";
import Modal from "react-modal";
import "./PropertyGallery.css";

// import local images
const dummyMedia = [
  { src: "https://picsum.photos/id/1018/1000/600", type: "image" }, // banner
  { src: "https://picsum.photos/id/1015/600/400", type: "image" }, // pool
  { src: "https://picsum.photos/id/1016/600/400", type: "image" }, // living room
  { src: "https://picsum.photos/id/1021/600/400", type: "image" }, // bedroom
  { src: "https://picsum.photos/id/1022/600/400", type: "image" }, // gym
];

const PropertyGallery = ({ media }) => {
  if (!media || media.length === 0) {
    media = dummyMedia;
  }

  const [selectedMedia, setSelectedMedia] = useState(null);
  const [isOpen, setIsOpen] = useState(false);

  const openModal = (mediaItem) => {
    if (!mediaItem) return;
    setSelectedMedia(mediaItem);
    setIsOpen(true);
  };

  const closeModal = () => {
    setIsOpen(false);
    setSelectedMedia(null);
  };

  return (
    <div className="property-gallery">
      <div className="gallery-left">
        <img
          src={media[0].src}
          alt="Main"
          onClick={() => openModal(media[0])}
          className="main-image"
        />
      </div>

      <div className="gallery-right">
        {media.slice(1, 5).map((item, idx) => (
          <div
            key={idx}
            className={`thumbnail ${idx === 4 ? "show-more" : ""}`}
            onClick={() => openModal(item)}
          >
            <img src={item.src} alt={`Thumbnail ${idx + 1}`} />
            {idx === 4 && <div className="overlay">Show more photos</div>}
          </div>
        ))}
      </div>

      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        className="lightbox"
        overlayClassName="overlay"
        ariaHideApp={false}
      >
        <button className="close-btn" onClick={closeModal} aria-label="Close modal">
          ×
        </button>

        <img src={selectedMedia?.src} alt="Large View" />
      </Modal>
    </div>
  );
};

export default PropertyGallery;
